import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { DataProvider } from '../../providers/data/data';
import { Collections, Content, User } from '../../app/app.models';
import { sortBy } from 'lodash';
import { AuthProvider } from '../../providers/auth/auth';

@IonicPage({
  segment: 'bloque-alt/:id'
})
@Component({
  selector: 'page-bloque-alt',
  templateUrl: 'bloque-alt.html',
})
export class BloqueAltPage {

  public id: string = this.navParams.get('id');

  public bloque: Content;
  public children: Content[];
  public completed: string[];
  public contentIds: string[] = [];

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private data: DataProvider,
    private auth: AuthProvider,
  ) {}

  ionViewDidLoad() {

    this.loadContent(this.id);

    this.auth.user$.subscribe(user => {
      if (user && !this.completed) this.completed = user.completedTasks;
      if (user && !this.children) this.loadChildren(this.id, user);
    })

  }

  get completedContent(): number {
    if (!this.completed) { return 0; }
    if (!this.contentIds) { return 0; }
    let counter = 0;
    for (const content of this.contentIds) {
      if (this.completed.includes(content)) { counter++; }
    }
    return counter;
  }

  async loadContent(id: string) {
    this.bloque = await this.data.getDocAlt<Content>(Collections.CONTENT, id);
  }

  async loadChildren(id: string, user: User) {
    const children = await this.data.getCollectionQuery<Content>(
      Collections.CONTENT,
      ref => ref.where('parent_id', '==', id).orderBy('sortIndex', 'asc')
    );
    this.children = children.filter(c => user.isPremium2019 ? true : c.liberadoInPrograma);
    this.contentIds = sortBy(children, 'sortIndex').map(t => t.event || 'NULL');
  }
}



